<template>
  <div class="fs-editor-wang5" v-bind="container">
    <Toolbar
      :ref="toolbarRef"
      style="border-bottom: 1px solid #ccc"
      :editor="editorRef"
      :default-config="toolbarConfigRef"
      :mode="mode"
      v-bind="toolbarAttrs"
    />
    <Editor
      style="height: 500px; overflow-y: hidden"
      :default-config="editorConfigRef"
      :mode="mode"
      v-bind="$attrs"
      @onChange="onChange"
      @onCreated="handleCreated"
    />
  </div>
</template>

<script lang="ts">
import "@wangeditor/editor/dist/css/style.css"; // å¼å¥ css

import { onBeforeUnmount, defineComponent, ref, shallowRef, onMounted, watch, computed, Ref } from "vue";
import { Editor, Toolbar } from "@wangeditor/editor-for-vue";

import { defaultConfig } from "../../type/config.js";
import _ from "lodash-es";
import { FsUploaderDoUploadOptions } from "../../../uploader/d/type";
import { useUi, utils } from "@fast-crud/fast-crud";
import { useUploader } from "../../../uploader";
import { DomEditor } from "@wangeditor/editor";
type InsertFnType = (url: string, alt?: string, href?: string) => void;
/**
 * wangEditor5ç»ä»¶å°è£
 * ææ¡£ï¼https://www.wangeditor.com/v5/for-frame.html#ä½¿ç¨-1
 */
export default defineComponent({
  name: "FsEditorWang5",
  components: { Editor, Toolbar },
  inheritAttrs: false,
  props: {
    /**
     * å®¹å¨éç½®
     * åè£editoråtoolbarçå¤é¨å®¹å¨éç½®ï¼å¯ä»¥è®¾ç½®classï¼styleç­
     */
    container: {
      type: Object
    },
    /**
     * editorçæ¨¡å¼
     */
    mode: {
      type: String
    },
    /**
     * å·¥å·æ¡éç½®
     */
    toolbarConfig: {
      type: Object
    },
    /**
     * å·¥å·æ¡styleç­éç½®
     */
    toolbarAttrs: {
      type: Object
    },
    /**
     * ç¼è¾å¨é»è®¤éç½®
     */
    editorConfig: {
      type: Object
    },
    /**
     * uploaderåæ°
     * éç½®åå¼å¯ä¸ä¼ 
     */
    uploader: {
      type: Object
    },
    /**
     * åªè¯»
     */
    readonly: {
      type: Boolean
    },
    /**
     * æ¯å¦ç¦ç¨
     */
    disabled: {
      type: Boolean
    }
  },
  emits: ["ready"],
  setup(props: any, ctx: any) {
    // ç¼è¾å¨å®ä¾ï¼å¿é¡»ç¨ shallowRef
    const editorRef = shallowRef();
    const toolbarRef: Ref = ref();
    const uploaderImplRef: Ref = ref();
    const valueHtml: Ref = ref("");

    function toggleEnabled(value: boolean) {
      if (!editorRef.value) {
        return;
      }
      if (value) {
        editorRef.value.enable();
      } else {
        editorRef.value.disable();
      }
    }

    watch(
      () => {
        return props.readonly;
      },
      (value) => {
        toggleEnabled(!value);
      },
      {
        immediate: true
      }
    );

    watch(
      () => {
        return props.disabled;
      },
      (value) => {
        toggleEnabled(!value);
      },
      {
        immediate: true
      }
    );

    const toolbarConfigRef = computed(() => {
      return _.merge({}, defaultConfig.wangEditor5.toolbarConfig, props.toolbarConfig);
    });

    const MENU_CONF: any = {};
    if (props.uploader) {
      async function doUpload(option: FsUploaderDoUploadOptions) {
        option.options = props.uploader;

        const { getUploaderImpl } = useUploader();
        let uploaderRef = await getUploaderImpl(option.options.type);
        if (uploaderRef == null) {
          ui.message.warn("Sorryï¼The uploader component is not ready yet");
          throw new Error("Sorryï¼The component is not ready yet");
        }

        return await uploaderRef?.upload(option);
      }

      async function customUpload(file: File, insertFn: InsertFnType) {
        // TS è¯­æ³
        // async customUpload(file, insertFn) {                   // JS è¯­æ³
        // file å³éä¸­çæä»¶
        // èªå·±å®ç°ä¸ä¼ ï¼å¹¶å¾å°å¾ç url alt href
        // æåæå¥å¾ç

        const item: any = {
          status: "uploading",
          progress: 0,
          message: undefined
        };

        const onProgress = (e: any) => {
          item.progress = e.percent;
        };
        const onError = (e: any) => {
          item.status = "error";
          item.message = "æä»¶ä¸ä¼ åºé:" + e.message;
          console.error(item.message, e);
        };
        const option = {
          file: file,
          fileName: file.name,
          onProgress,
          onError
        };

        const res = await doUpload(option);
        let url = res?.url;
        if (props.uploader?.buildUrl) {
          url = await props.uploader.buildUrl(res);
        }
        // ä¸ä¼ å¾çï¼è¿åç»æï¼å°å¾çæå¥å°ç¼è¾å¨ä¸­
        insertFn(url);
      }

      MENU_CONF.uploadImage = {
        customUpload
      };
      MENU_CONF.uploadVideo = {
        customUpload
      };
    }

    const editorConfigRef = computed(() => {
      return _.merge(
        {
          placeholder: "è¯·è¾å¥åå®¹...",
          MENU_CONF
        },
        defaultConfig.wangEditor5.editorConfig,
        props.editorConfig
      );
    });

    // ç»ä»¶éæ¯æ¶ï¼ä¹åæ¶éæ¯ç¼è¾å¨
    onBeforeUnmount(() => {
      const editor = editorRef.value;
      if (editor == null) return;
      editor.destroy();
    });

    const handleCreated = (editor: any) => {
      editorRef.value = editor; // è®°å½ editor å®ä¾ï¼éè¦ï¼
      toggleEnabled(!props.readonly && !props.disabled);
      ctx.emit("ready");
    };

    function getEditorRef() {
      return editorRef;
    }
    function getToolRef() {
      return toolbarRef;
    }
    const { ui } = useUi();
    let formItemContext = ui.formItem.injectFormItemContext();
    function onChange() {
      formItemContext.onChange();
    }

    onMounted(() => {});

    return {
      uploaderImplRef,
      getToolRef,
      getEditorRef,
      toolbarRef,
      editorRef,
      valueHtml,
      toolbarConfigRef,
      editorConfigRef,
      handleCreated,
      onChange
    };
  }
});
</script>
<style lang="less">
.fs-editor-wang5 {
  border: 1px solid #eee;
  &.w-e-full-screen-container {
    z-index: 10000;
  }
}
</style>
